body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/* Custom CSS for Lava Lamp Effect */
/* src/index.css */


/* Custom CSS for Lava Lamp Effect */
.bg-goo-wrapper {
  /* Slightly reduced contrast for subtlety with mix-blend-overlay */
  filter: contrast(35); /* Adjusted from 40 to 35 - fine-tune between 25 and 40 */
  -webkit-transform: translate3d(0,0,0); /* Performance hack */
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
